<script>
import FooterArea from './components/FooterArea.vue'
import {mapState} from "vuex";
import Airtable from "./airtable.js";
import Email from "./smtp.js";
import ImageCompressor from "js-image-compressor";
import airtable from './airtable';
// import { VueRecaptcha } from 'vue-recaptcha';
export default {
  name: 'App',
  components: {
    FooterArea,
    // VueRecaptcha
  },
  watch: {
    // ONLY ENABLE WHEN YOU KNOW HOW TO DO THE LAZY LOAD
    $route() {
      this.removeEventListeners();
      this.addEventListeners();
    }
  },

  computed: {
    ...mapState(["loading", "lang", "meetsLoadedIndex", "maxMeetsLoaded", "filteredMeets"])
  },
  methods: {
    onHomePage() {
      return window.location.hash === `#/`;
    },
    scrollEvent(e) {
      this.scroll = e;
      // let windowPos = window.scrollY;
      let windowPos = document.getElementById("app").scrollTop;
      this.windowscroll = windowPos;
      // let loadMoreOffsetPos = document.getElementById("app").getBoundingClientRect().height - (document.getElementById("app").innerHeight + 100);
      // if (this.disableScroll || this.meetsLoadedIndex > this.maxMeetsLoaded) return;

      // this.$nextTick(() => {
      //   if (windowPos >= loadMoreOffsetPos && this.meetsLoadedIndex < this.filteredMeets.length) {
      //     // console.log(this.filteredMeets, this.meetsLoadedIndex)
      //     // console.log("load more")
      //     this.disableScroll = true;
      //     setTimeout(() => {
      //       this.$store.commit("setMeetsLoadedIndex", this.meetsLoadedIndex + 10);
      //       this.disableScroll = false;
      //     }, 500);
      //   }
      // })
    },
    touchstart(e) {
      this.touchstartPos = e.touches[0].clientY;
      if (document.getElementById("app").scrollTop === 0) {
        document.getElementById("app").addEventListener("touchmove", this.touchmove);
        document.getElementById("app").addEventListener("touchend", this.touchend); 
      }
    },
    touchmove(e) {
      let appinnerMove = Math.min((e.touches[0].clientY - this.touchstartPos), window.innerHeight / 6);
      // if direction is up AND you are already at the top
      if (e.touches[0].clientY > this.lastTouch && document.getElementById("app").scrollTop === 0) {
        document.getElementById("appinner").style.transform = `translate3d(0,${appinnerMove}px,0)`;
        document.getElementById("appinner").style.transition = `none`;

        let divTransform = parseInt(document.getElementById("appinner").style.transform.split("translate3d(0px, ")[1].split("px")[0]);

        // if you pull back up and you're at the top
        if (divTransform >= Math.floor(window.innerHeight / 6)) {
          this.showRefresh = true;
        }
      }

      this.lastTouch = e.touches[0].clientY;

    },
    touchend() {
      // this.showRefresh = false;
      document.getElementById("app").removeEventListener("touchmove", this.touchmove);
      document.getElementById("app").removeEventListener("touchend", this.touchend); 

      if (this.showRefresh) {
          document.getElementById("app").removeEventListener("touchstart", this.touchstart);
          this.$nextTick(() => {
            this.$geoAPI.fetchLocation2();

            Promise.all([
              airtable.init_airtable2(), 
              airtable.init_shop(),
              airtable.init_shop_items(),
              airtable.init_featured(),
              airtable.init_airtable(),
            ]).then(() => {
              this.showRefresh = false;
              document.getElementById("app").addEventListener("touchstart", this.touchstart);
              document.getElementById("appinner").style.transform = `translate3d(0,0,0)`;
              document.getElementById("appinner").style.transition = `transform .5s ease`;
            })
            
            // window.location.reload();
          })
      } else {
        document.getElementById("appinner").style.transform = `translate3d(0,0,0)`;
        document.getElementById("appinner").style.transition = `transform .5s ease`;
      }
    },
    removeEventListeners() {
      document.getElementById("app").removeEventListener("mousewheel", this.scrollEvent);
      document.getElementById("app").removeEventListener("scroll", this.scrollEvent);
    },
    addEventListeners() {
      let type = (() => {
        if (window.navigator.userAgent.includes("iPhone") ||
        window.navigator.userAgent.includes("Mobile")) {
          return "mobile";
        }
        return "desktop";
      })();
      if (window.location.pathname === "/" || window.location.hash === "#/") {
        switch(type) {
          case "mobile": {
            document.getElementById("app").addEventListener("scroll", this.scrollEvent);
            document.getElementById("app").addEventListener("touchstart", this.touchstart);
            break;
          }
          case "desktop": {
            document.getElementById("app").addEventListener("mousewheel", this.scrollEvent);
            break;
          }
        }
       
      } 
    },
    doimageCompress() {
      let i = 0;
      setInterval(() => {
        this.imageCompress(i);
        i++
      }, 1000);
    },
    imageCompress(i) {
        let url = [...this.$meets][i].Image[0].url;
        let id = [...this.$meets][i].id;
        console.log(id, url);

        fetch(url).then((res) => {
          return res.blob();
        }).then((blob) => {
          const file = new File([blob], 'image.jpeg', {type: blob.type});
          console.log(file);
          // console.log(meet.Image[0].url);
          // let file = meet.Image[0].url;
          var options = {
            file: file,
            quality: 1,
            maxWidth: 700,
            maxHeight: 700,

            // Callback before compression
            beforeCompress: function (image) {
              console.log('Image size before compression:', image.size);
              console.log('mime type:', image.type);
            },

            // Compression success callback
            success: function (image) {
              console.log('result:', image)
              console.log('Image size after compression:', image.size);
              console.log('mime type:', image.type);
              console.log('Actual compression ratio:', ((file.size-image.size) / file.size * 100).toFixed(2) +'%');
              Airtable.uploadImageToField(image, id);
            }
          };

          new ImageCompressor(options);
        });
    },
    copyMeetsTodayText() {
      let date = new Date().toString().split(" ");
      let text = `EVENTS on ${date[0].toUpperCase()}. ${date[1]} ${date[2]}, ${date[3]}: \n \n`;
      [...this.$meets].filter(i => i.Today).forEach(meet => {
        text += `MEET: ${meet.Name}
WHERE: ${meet.Address}
TIME: ${meet.Time}
\n`;
      });
      navigator.clipboard.writeText(text);
    },
    copyMeetsTomorrowText() {
      let text = `EVENTS on  \n \n`;
      [...this.$meets].filter(i => i.Tomorrow).forEach(meet => {
        text += `MEET: ${meet.Name}
WHERE: ${meet.Address}
TIME: ${meet.Time}
\n`;
      });
      navigator.clipboard.writeText(text);
    },
    async submitForm() {
      
      new Promise((resolve) => {
        if (this.$refs.img.files[0]) {
          var axios = require('axios');
          var fileinput = this.$refs.img.files[0];
          const formData = new FormData();
          formData.append( "image", fileinput ); // has to be named 'image'!
          axios.post( 'https://api.imgbb.com/1/upload?key=3314dc7a8197bab1f6f1b512c8595eb7', formData )
            .then( res => { 
              resolve(res.data.data.display_url);
            } )
        } else {
          resolve('');
        }
      }).then((imgUrl) => { 
        
        var submission = {
          name: this.$refs.name.value,
          date: this.$refs.date.value,
          ig: this.$refs.ig.value,
          website: this.$refs.website.value,
          img: imgUrl
        }
        Email.send({
          SecureToken : "07b8bda7-423f-47ec-9a4d-8705fe0b3747",
          To : 'carllective@rebj.ca',
          From : "rebjanec@gmail.com",
          Subject : "carllective.ca submission",
          Body : `
          Name: ${this.$refs.name.value} <br/>
          Date: ${this.$refs.date.value} <br/>
          Instagram: ${this.$refs.ig.value} <br/>
          Website: ${this.$refs.website.value} <br/>
          Image: <img src="${imgUrl}"/>
          `
        }).then(() => {
          this.successMessage = "Thank you! Your submission has been sent successfully";
          this.$refs.form.reset();
          Airtable.submitEvent(submission);
          
          setTimeout(() => {
            this.successMessage = null;
            this.showSubmitForm = false;
          }, 5000);
        })
        

      })
    }
  },
  data() {
    return {
      scroll: null,
      showSubmitForm: false,
      successMessage: null,
      disableScroll: false,
      windowscroll: 0,
      touchstartPos: 0,
      showRefresh: false,
      lastTouch: null
    }
  },
  mounted() {
    // console.log(window.location.pathname);s
  },
  
}
</script>

