  getNextMonthTomorrow(date) {
    switch(date) {
      case "Jan 31": {
        return "Feb 1";
      }
      case "Feb 28": {
        return "Mar 1";
      }
      case "Mar 31": {
        return "Apr 1";
      }
      case "Apr 30": {
        return "May 1";
      }
      case "May 31": {
        return "June 1";
      }
      case "June 30": {
        return "July 1";
      }
      case "July 31": {
        return "Aug 1";
      }
      case "Aug 31": {
        return "Sep 1";
      }
      case "Sep 30": {
        return "Oct 1";
      }
      case "Oct 31": {
        return "Nov 1";
      }
      case "Nov 30": {
        return "Dec 1";
      }
      case "Dec 31": {
        return "Jan 1";
      }
      default: {
        return date.split(" ")[0] + " " + (parseInt(date.split(" ")[1]) + 1);
      }
    }
  }
