import Vue from 'vue'
import App from './App.vue'
import airtable from './airtable';
import geo from './geo';
import store from './store';
import router from './router';
import VueHead from 'vue-head';
Vue.config.productionTip = false;
Vue.use(VueHead);

if (window.location.href.includes("fr.")) {
  store.commit("setLang", "fr");
} else {
  store.commit("setLang", "en");
}
// store.commit("setLang", "fr");


function initVue() {
  return new Promise((res) => {
      new Vue({
        store,
        router,
        render: h => h(App),
      }).$mount('#app');
      res();
    })
}
// airtable.init_airtable();
geo.fetchLocation2();
Vue.prototype.$geoAPI = geo;

Vue.prototype.$geoAPI.init({format: 'json'}).then((Promise.all([
  // airtable.queryMeetsByProvince(store.state.activeProvince.en),
  airtable.init_airtable2(), 
  
  airtable.init_shop(),
  airtable.init_shop_items(),
  airtable.init_featured(),
  airtable.init_airtable()
])
.then(() => initVue())));

